// footer
.site-footer {
	background: url(../img/bg-footer.jpg) center 0;
	color: #fff;
	.col-footer-logo {
		padding-top: 40px;
	}
	.social-menu {
		margin-top: 42px;
		li {
			margin: 5px;
			a {
				padding: 5px;
				color: #fff;
					background: transparent !important;
				&:hover {
					color: $yellow;
				}
			}
		}
	}
}
.newsletter-footer-form-wrapper {
	position: relative;
	max-width: 255px;
	width: 255px;
	background: #252e33;
	color: #fff;
	padding: 32px 20px 20px;
	&:before {
		content: '';
		display: block;
		height: 10px;
		background: url(../img/bg-footer-nl.png) no-repeat center 0px;
		left: 0;
		top: -10px;
		position: absolute;
		right: 0;
	}
	.newsletter-title {
		font-size: 20px;
		font-weight: 700;
		margin-bottom: 20px;
		text-transform: uppercase;
		text-align: center;
	}
	input[type=text] {
		background: #4f5a61;
		color: #fff;
		font-size: 13px;
		height: 34px;
		border-radius: 0;
		border: 0;
	}
	button {
		height: 34px;
		width: 100%;
		border: 2px solid $yellow;
		color: $yellow;
		cursor: pointer;
		outline: none;
		background: transparent;
	}
}