.bootstrap { display: flex; }

/** jui-rewrite */
.jui { transition: all 1s; justify-content: space-between; order: 2; width: auto; display: flex; border-radius: 5px; }

@font-face { font-family: 'MuseoSC'; src: url("fonts/MuseoSansCyrl_300.eot"); src: local("☺"), url("fonts/MuseoSansCyrl_300.woff") format("woff"), url("fonts/MuseoSansCyrl_300.ttf") format("truetype"), url("fonts/MuseoSansCyrl_300.svg") format("svg"); font-weight: 300; font-style: normal; }

@font-face { font-family: 'MuseoSC'; src: url("fonts/MuseoSansCyrl_500.eot"); src: local("☺"), url("fonts/MuseoSansCyrl_500.woff") format("woff"), url("fonts/MuseoSansCyrl_500.ttf") format("truetype"), url("fonts/MuseoSansCyrl_500.svg") format("svg"); font-weight: 500; font-style: normal; }

@font-face { font-family: 'MuseoSC'; src: url("fonts/MuseoSansCyrl_700.eot"); src: local("☺"), url("fonts/MuseoSansCyrl_700.woff") format("woff"), url("fonts/MuseoSansCyrl_700.ttf") format("truetype"), url("fonts/MuseoSansCyrl_700.svg") format("svg"); font-weight: bold; font-style: normal; }

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { border: 0; vertical-align: baseline; margin: 0; padding: 0; }

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section { display: block; }

ol, ul { list-style: none; }

blockquote, q { quotes: none; }

blockquote:before, blockquote:after, q:before, q:after { content: none; }

table { border-collapse: collapse; border-spacing: 0; }

.flexbox { display: -webkit-box; display: -moz-box; display: -ms-flexbox; display: -webkit-flex; display: flex; }

.flex-wrap { -webkit-flex-wrap: wrap; -moz-box-flex-wrap: wrap; -ms-flex-wrap: wrap; flex-wrap: wrap; }

.just-around { -ms-flex-pack: distribute; -webkit-justify-content: space-around; justify-content: space-around; }

.just-between { -webkit-box-pack: justify; -webkit-justify-content: space-between; -moz-box-pack: justify; -ms-flex-pack: justify; justify-content: space-between; }

.row-search-form { padding-top: 20px; padding-bottom: 20px; }

.search-text-group { flex: 1; }

.search-text-group input.form-control { width: 100%; }

.main-filter-wrap { height: 466px; position: relative; background: url(../img/bg-slider.jpg) no-repeat center 0; }

.main-filter-wrap .slider-main-block { position: absolute; width: 1500px; max-width: 100%; z-index: 1; }

.main-filter-wrap .filter-main-container { position: relative; z-index: 2; }

.main-filter-wrap .filter-main-container .block-filter-main { margin-top: 40px; }

.main-filter-wrap .filter-main-container .filter-form-row { margin-bottom: 27px; }

.list-categories { margin-bottom: 40px; }

.list-categories .item-cat { height: 150px; width: 150px; background: #efefef; margin: 10px; position: relative; text-align: center; border: 1px solid #d9d9d9; transition: background 100ms; }

.list-categories .item-cat a { text-decoration: none; }

.list-categories .item-cat ._count { position: absolute; padding: 3px 10px; color: #2c363c; background: #f64e21; top: 0; right: 0; font-size: 13px; font-weight: 700; }

.list-categories .item-cat ._img { display: table-cell; width: 150px; padding-top: 25px; height: 107px; vertical-align: middle; }

.list-categories .item-cat ._title { color: #2c363c; text-transform: uppercase; display: block; line-height: 1; text-decoration: none !important; font-size: 12px; }

.list-categories .item-cat.other-cat { width: 100%; height: 66px; vertical-align: middle; }

.list-categories .item-cat.other-cat a { display: -webkit-box; display: -moz-box; display: -ms-flexbox; display: -webkit-flex; display: flex; }

.list-categories .item-cat:hover { background: #f64e21; }

.list-categories .item-cat:hover ._count { background: #2c363c; color: #fff; }

.site-footer { background: url(../img/bg-footer.jpg) center 0; color: #fff; }

.site-footer .col-footer-logo { padding-top: 40px; }

.site-footer .social-menu { margin-top: 42px; }

.site-footer .social-menu li { margin: 5px; }

.site-footer .social-menu li a { padding: 5px; color: #fff; background: transparent !important; }

.site-footer .social-menu li a:hover { color: #f64e21; }

.newsletter-footer-form-wrapper { position: relative; max-width: 255px; width: 255px; background: #252e33; color: #fff; padding: 32px 20px 20px; }

.newsletter-footer-form-wrapper:before { content: ''; display: block; height: 10px; background: url(../img/bg-footer-nl.png) no-repeat center 0px; left: 0; top: -10px; position: absolute; right: 0; }

.newsletter-footer-form-wrapper .newsletter-title { font-size: 20px; font-weight: 700; margin-bottom: 20px; text-transform: uppercase; text-align: center; }

.newsletter-footer-form-wrapper input[type=text] { background: #4f5a61; color: #fff; font-size: 13px; height: 34px; border-radius: 0; border: 0; }

.newsletter-footer-form-wrapper button { height: 34px; width: 100%; border: 2px solid #f64e21; color: #f64e21; cursor: pointer; outline: none; background: transparent; }

