
.row-search-form {
	padding-top: 20px;
	padding-bottom: 20px;
}

.search-text-group {
	flex: 1;
	input.form-control {
		width: 100%;
	}
}

.main-filter-wrap {
	height: 466px;
	position: relative;
	background: url(../img/bg-slider.jpg) no-repeat center 0;

	.slider-main-block {
		position: absolute;
		width: 1500px;
		max-width: 100%;
		z-index: 1;
	}
	.filter-main-container {
		position: relative;
		z-index: 2;

		.block-filter-main {
			margin-top: 40px;
		}
		.filter-form-row {
			margin-bottom: 27px;
		}

	}
}